h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}
a {
  background: transparent;
  text-decoration: none;
}
a:active, /* активная/посещенная ссылка */
a:hover  /* при наведении */ {
  text-decoration: none;
  //   color: #666;
}
